
.slider {
  overflow: hidden;
  position: relative;
}
.swiper {
  overflow: hidden;
  position: relative;
}
.swiper .item {
  float: left;
  position: relative;
}
.swiper .item a {
  display: block;
  width: 100%;
  height: 100%;
}
.swiper .item .img {
  display: block;
  width: 100%;
  height: 100%;
  background: center center no-repeat;
  background-size: cover;
}
.swiper .item .desc {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1.4em;
  font-size: 16px;
  padding: 20px 50px 12px 13px;
  background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, .7) 100%);
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, .7) 100%);
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, .5);
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
}
.indicator {
  position: absolute;
  right: 15px;
  bottom: 10px;
}
.indicator a {
  float: left;
  margin-left: 6px;
}
.icon_dot {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #d0cdd1;
}
.icon_dot.active {
  background-color: #04BE02;
}



